import {
    Cell,
    Group,
    PanelHeader,
    List,
    Avatar,
    Button,
    Placeholder,
    PanelHeaderBack,
    PullToRefresh
} from "@vkontakte/vkui";
import React from "react";
import Icon24MoreHorizontal from '@vkontakte/icons/dist/24/more_horizontal';
import bridge from '@vkontakte/vk-bridge';
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import fire from "../../firebase_config";


const Tmp = (e) =>{
    if (e.p.length){
        return e.p.map(p =>
            <Cell
                before={<Avatar src={p.photo_100} size={72} />}
                size="l"
                description="камень, ножницы, бумага"
                //asideContent={<Icon24MoreHorizontal />}
                bottomContent={
                    <div style={{ display: 'flex' }}>
                        <Button onClick={() => {e.accept(p.id)}} size="m">Принять</Button>
                        <Button onClick={() => {e.remove(p)}} size="m" mode="secondary" style={{ marginLeft: 8 }}>Скрыть</Button>
                    </div>
                }
            >{p.first_name+' '+p.last_name}</Cell>
        );
    }
    else{
        return (<Placeholder
            icon={<Icon56ErrorOutline width={128} height={128}/>}
            header="Новых приглашений нет"
            //action={<Button size="l">Подключить сообщества</Button>}
        >
            Попросите друга отправить вам приглашение, <br/>и оно появится здесь
        </Placeholder>);
    }
};


class Invites extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            invites: [],
            fetching: false
        };
        this.nniga = this.nniga.bind(this);
        this.removeItem = this.removeItem.bind(this);
    }

    componentDidMount() {
        this.nniga();
    }

    removeItem(id){
        let a = this.state.invites.splice(this.state.invites.indexOf(id), 1);
        let b = [];
        for (let i = 0; i < a.length; i++){
            b[i] = a[i].id;
        }
        b.splice(this.state.invites.indexOf(id), 1);
        fire.firestore().collection('users').doc(this.props.state.userId.toString()).update({
            invites: b
        }).then(s => this.nniga())
    }

    nniga (){
        this.setState({
            fetching: true
        })
        if(this.props.state.invites.length){
            bridge.send("VKWebAppGetAuthToken", {"app_id": 7390057, "scope": "friends"}).then(q => {
                bridge.send("VKWebAppCallAPIMethod", {"method": "users.get", "request_id": "32test", "params": {"user_ids": this.props.state.invites.join(','), "v":"5.103", "access_token":q.access_token, "fields": "photo_100"}}).then(
                    v => {
                        this.setState({
                            invites: v.response,
                            fetching: false
                        });
                        console.log(this.state.invites);
                    })
            });
        }
        else{
            setTimeout(this.nniga, 2500);
        }
    }

    render() {
        return (
            <div>
                <PanelHeader left={<PanelHeaderBack onClick={() => this.props.menu()}/>}>Приглашения</PanelHeader>
                <Group>
                    <List>
                        <PullToRefresh style={{minHeight: '90vh'}} onRefresh={this.nniga} isFetching={this.state.fetching}>
                            <Tmp accept={this.props.accepted} remove={this.removeItem} p={this.state.invites}/>
                        </PullToRefresh>
                    </List>
                </Group>
            </div>)
    }
}


export default Invites;