import {
    Avatar, Button,
    Card,
    CardGrid,
    FixedLayout,
    Group,
    Header,
    PanelHeader,
    PanelHeaderBack,
    Spinner
} from "@vkontakte/vkui";
import React from "react";
import bridge from "@vkontakte/vk-bridge";
import fire from "../../firebase_config";
import Icon24Report from '@vkontakte/icons/dist/24/report';
import IconRobot from "../emoji/IconRobot";
import $ from "jquery";
import randInt from "../../functions/randInt";

const urls = [
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/raised-fist_270a.png',
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/victory-hand_270c.png',
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/raised-hand_270b.png'
];

class WithFriend extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            room: {
                empty: true
            }
        };
        if(this.props.state.roomqw){
            fire.firestore().collection('rooms').doc(this.props.state.roomqw.toString()).onSnapshot((snapshot => {
                this.setState({
                    room: snapshot.data()
                })
            }));
        }
    }

    jopa(e){
        if (this.props.state.round){
            return;
        }
        $('.left_hand,.right_hand').removeClass('animated');
        if(this.props.state.roomqw){
            fire.firestore().collection('rooms').doc(this.state.room.user.toString()).update({
                friendDone: 1,
                friendHand: e
            });
        }
        else{
            fire.firestore().collection('rooms').doc(this.state.room.user.toString()).update({
                userDone: 1,
                userHand: e
            });
        }
        if(this.state.room.userDone || this.state.room.friendDone){
            $('.left_hand,.right_hand').addClass('animated');
        }
    }

    removeInvite(q){
        let frId = q.room.friend;
        fire.firestore().collection('users').doc(frId.toString()).get().then(
            k => {
                let invs = k.data().invites;
                invs.splice(invs.indexOf(frId), 1);
                fire.firestore().collection('users').doc(frId.toString()).update({
                    invites: invs
                }).then(r => {
                    this.setState({
                        room: {
                            empty: true
                        }
                    })
                });
            }
        );
    }

    invite(q){
        bridge.send("VKWebAppGetFriends", {}).then(
            r => {
                console.log(r);
                let obj = {
                    confirmed: 0,
                    friend: r.users[0].id,
                    user: q.userId,
                    userHand: 0,
                    friendHand: 0,
                    userScore: 0,
                    friendScore: 0,
                    userDone: 0,
                    friendDone: 0,
                    empty: false,
                    userAva: this.props.state.userAva,
                    userName: this.props.state.userName
                };
                fire.firestore().collection('rooms').doc(q.userId.toString()).set(obj).then(e => {
                    fire.firestore().collection('users').doc(r.users[0].id.toString()).get().then(
                        k => {
                            if (k.exists) {
                                let friend_invites = k.data().invites;
                                if(friend_invites.indexOf(q.userId) === -1){
                                    friend_invites[friend_invites.length] = q.userId;
                                    fire.firestore().collection('users').doc(r.users[0].id.toString()).update({
                                        invites: friend_invites
                                    }).then(r => {
                                        fire.firestore().collection('rooms').doc(q.userId.toString()).onSnapshot((snapshot => {
                                            this.setState({
                                                room: snapshot.data()
                                            })
                                        }));
                                        this.setState({room: obj});
                                    });
                                }
                                else{
                                    this.props.setSnackbar(['Приглашение уже отправлено', <Icon24Report fill="var(--destructive)"/>]);
                                }
                            }
                            else{
                                this.props.setSnackbar(['Пользователь не зарегистрирован', <Icon24Report fill="var(--destructive)"/>]);
                            }
                        }
                    );
                    // fire.firestore().collection('invites').doc(r.users[0].id.toString()).set({room: q.userId}).then(
                    //     w => console.log(w)
                    // );
                });
                // bridge.send("VKWebAppGetAuthToken", {"app_id": 7390057, "scope": "friends"}).then(q => {
                //     bridge.send("VKWebAppCallAPIMethod", {"method": "users.get", "request_id": "32test", "params": {"user_ids": r.users[0].id.toString(), "v":"5.103", "access_token":q.access_token}}).then(
                //         v => {
                //             this.setState({
                //
                //             })
                //         }
                //     );
                // });
            }
        );
    }

    render() {
        return (<div>
            <PanelHeader left={<PanelHeaderBack onClick={() => this.props.menu()}/>}>Против друга</PanelHeader>
            <Group>
                {this.state.room.confirmed ? (
                    <CardGrid className="battle_avas" style={{borderRadius: 5, padding: 10}}>
                        <Card size="l">
                            <Card style={{paddingTop: 15}} className="text-middle" size="s">
                                <div className="test">
                                    <Avatar src={this.state.room.friendAva ? this.state.room.friendAva : 'https://vk.com/images/camera_200.png'}/>
                                    <Header className="no-flex" mode="secondary">{this.state.room.friendName ? this.state.room.friendName : 'лох'}</Header>
                                </div>
                                <div className="test2 hidden">{this.state.room.freindScore}</div>
                            </Card>
                            <Card style={{marginTop: 42}} className="text-middle" size="s">
                                <Header className="no-flex" mode="secondary">против</Header>
                            </Card>
                            <Card style={{paddingTop: 15}} className="text-middle" size="s">
                                <div className="test">
                                    <Avatar src={this.state.room.userAva ? this.state.room.userAva : 'https://vk.com/images/camera_200.png'}/>
                                    <Header className="no-flex" mode="secondary">{this.state.room.userName ? this.state.room.userName : 'тоже лох'}</Header>
                                </div>
                                <div className="test2 hidden">{this.state.room.userScore}</div>
                            </Card>
                        </Card>
                        <div className="clear-fix"/>
                    </CardGrid>
                ) : (
                    <CardGrid>
                        <Card size="l">
                            {this.state.room.empty ?
                                (<Button size="xl" onClick={() => {this.invite(this.props.state)}}>Пригласить друга</Button>)
                                :
                                (<Button before={<Spinner size="small" />} onClick={() => {this.removeInvite(this.state)}} mode="secondary" size="xl">Ожидаем ответа</Button>)}
                        </Card>
                    </CardGrid>
                )
                }
            </Group>
            <Group style={{padding: 30}}>
                <img
                    className={(this.state.userDone && this.state.friendDone) ? "left left_hand animated" : "left left_hand"}
                    alt="hui"
                    src={(this.state.userDone && this.state.friendDone) ? urls[this.state.friendHand] : urls[0]}/>
                <img
                    className={(this.state.userDone && this.state.friendDone) ? "right right_hand animated" : "right right_hand"}
                    alt="jopa"
                    src={(this.state.userDone && this.state.friendDone) ? urls[this.state.userHand] : urls[0]}/>
                <div className="clear-fix"/>
            </Group>
            <FixedLayout style={
                this.props.state.round ? {
                    transition: '.2s',
                    transform: 'translateY(100%)'
                } : {
                    transition: '.2s'
                }
            } vertical="bottom">
                <CardGrid className="battle_avas" style={{borderRadius: 5, padding: 10}}>
                    <Card style={this.props.state.roomqw ? {transform: "scaleX(-1)"} : {}} size="l">
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(0)}} className="selectHand" alt="jopa" src={urls[0]}/>
                        </Card>
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(1)}} className="selectHand" alt="jopa" src={urls[1]}/>
                        </Card>
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(2)}} className="selectHand" alt="jopa" src={urls[2]}/>
                        </Card>
                    </Card>
                    <div className="clear-fix"/>
                </CardGrid>
            </FixedLayout>
        </div>)
    }
}

export default WithFriend;