import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import {
    View,
    Panel,
    Avatar,
    Snackbar
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import bridge from '@vkontakte/vk-bridge';
import fire from './firebase_config';
import Stats from "./components/panels/Stats";
import Menu from "./components/panels/Menu";
import WithBot from "./components/panels/WithBot";
import Modal from "./components/Modal";
import WithFriend from "./components/panels/withFriend";
import Invites from "./components/panels/Invites";

class Main extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'menu',
            leftHand: 0,
            rightHand: 0,
            userScore: 0,
            botScore: 0,
            activeModal: null,
            round: false,
            stats: {
                wins: 0,
                loses: 0
            },
            friendMatch: 0,
            invites: [],
            snackbar: null
        };
        this.stateWithBot = this.stateWithBot.bind(this);
        this.menu = this.menu.bind(this);
        this.setActivePanel = this.setActivePanel.bind(this);
        this.setSnackbar = this.setSnackbar.bind(this);
        this.inviteAccepted = this.inviteAccepted.bind(this);
    }

    menu(){
        this.setState({
            activePanel: 'menu',
            roomqw: 0
        })
    }

    setSnackbar(p){
        this.setState({
            snackbar: (
                <Snackbar
                    layout="vertical"
                    onClose={() => this.setState({ snackbar: null })}
                    before={p[1]}
                >
                    {p[0]}
                </Snackbar>
            )
        });
        setTimeout(() => {
            this.setState({snackbar: null})
        }, 3000)
    }

    inviteAccepted(id){
        console.log(id);
        fire.firestore().collection('rooms').doc(id.toString()).update({
            confirmed: 1,
            friendAva: this.state.userAva,
            friendName: this.state.userName
        }).then(s => {
            let a = this.state.invites.splice(this.state.invites.indexOf(id), 1);
            let b = [];
            for (let i = 0; i < a.length; i++){
                b[i] = a[i].id;
            }
            b.splice(this.state.invites.indexOf(id), 1);
            fire.firestore().collection('users').doc(this.state.userId.toString()).update({
                invites: b
            }).then(
                z => this.setState({
                        activePanel: 'with_friend',
                        roomqw: id
                    })
            );
        });
    }

    componentDidMount() {
        bridge.send("VKWebAppInit", {}).then(
            r => {
                bridge.send("VKWebAppGetUserInfo", {}).then(
                    r => {
                        console.log(r);
                        this.setState({
                            userAva: r.photo_200,
                            userName: r.first_name,
                            userId: r.id
                        });
                        console.log(this.state);
                        fire.firestore().collection('users').doc(r.id.toString()).onSnapshot((snapshot => {
                            //console.log(snapshot);
                            if(!snapshot.exists){
                                fire.firestore().collection('users').doc(r.id.toString()).set({
                                    balance: 0,
                                    wins: 0,
                                    loses: 0,
                                    invites: []
                                }).then(r => console.log('done'));
                            }
                            let userData = snapshot.data();
                            this.setState({
                                stats: {
                                    wins: userData.wins,
                                    loses: userData.loses
                                },
                                invites: userData.invites
                            })
                        }));
                    }
                );
            }
        );
        window.addEventListener('popstate', () => {
            if(this.state.activePanel !== 'menu'){
                this.setState({
                    activePanel: 'menu'
                })
            }
            else{
                window.history.back();
            }
        })
    }

    setActivePanel(p){
        this.setState({
            activePanel: p
        })
    }

    stateWithBot(p){
        //leftHand, rightHand, userScore, botScore, round, activeModal, stats
        this.setState({
            leftHand: p[0],
            rightHand: p[1],
            userScore: p[2],
            botScore: p[3],
            round: p[4],
            activeModal: p[5],
            stats: p[6]
        });
    }

    setActiveModal = (e) => {
        this.setState({
            activeModal: e,
            round: false
        })
    }

    render() {
        return (
            <View activePanel={this.state.activePanel} id="menu" modal={<Modal state={this.state} setActiveModal={this.setActiveModal}/>}>
                <Panel id="menu">
                    <Menu state={this.state} panel={this.setActivePanel}/>
                    {this.state.snackbar}
                </Panel>
                <Panel id="stats">
                    <Stats state={this.state} menu={this.menu}/>
                    {this.state.snackbar}
                </Panel>
                <Panel id="with_bot">
                    <WithBot state={this.state} menu={this.menu} return={this.stateWithBot}/>
                    {this.state.snackbar}
                </Panel>
                <Panel id="with_friend">
                    <WithFriend setSnackbar={this.setSnackbar} state={this.state} menu={this.menu} return={this.stateWithBot}/>
                    {this.state.snackbar}
                </Panel>
                <Panel id="invites">
                    <Invites accepted={this.inviteAccepted} state={this.state} menu={this.menu}/>
                    {this.state.snackbar}
                </Panel>
            </View>
        )
    }
}

ReactDOM.render(
  <React.StrictMode>
      <Main/>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWAd
serviceWorker.unregister();
